import React from "react";
import "../css/calculator.css";
import { useSelector } from "react-redux";
function Calculator() {
  // const value = '2'
  const [value, setValue] = React.useState("2");
  const count = useSelector((state) => state.counter.step);

  const clearFn = () => {
    if (value.length > 0) {
      const spliteArray = value.split("");
      spliteArray.pop();
      // console.log(spliteArray.join(''));
      setValue(spliteArray.join(""));
      // console.log(value);
      // setValue(value.split('').pop())
    } else {
      alert("Nothing inside textbox");
    }
  };

  const calculate = () => {
    try {
      const result = parseInt(eval(value));
      setValue(result);
    } catch (e) {
      console.log(e);
      alert("invaid expression");
    }
  };
  // const display = (key) => {
  // alert(key)
  // document.getElementById('display-area').value = key;
  // setValue(value+key)
  // }
  return (
    <div className="calculator">
      <div>
        {/* {count} */}
        <input id="display-area" type="text" value={value} readOnly />
      </div>
      <div>
        <button onClick={() => setValue(value + "/")}>/</button>
        <button onClick={() => setValue(value + "*")}>*</button>
        <button onClick={() => setValue(value + "-")}>-</button>
        <button onClick={() => setValue(value + "+")}>+</button>
      </div>
      <div>
        <button onClick={() => setValue(value + "7")}>7</button>
        <button onClick={() => setValue(value + "8")}>8</button>
        <button onClick={() => setValue(value + "9")}>9</button>
        <button onClick={clearFn}>C</button>
      </div>
      <div>
        <button onClick={() => setValue(value + "4")}>4</button>
        <button onClick={() => setValue(value + "5")}>5</button>
        <button onClick={() => setValue(value + "6")}>6</button>
        <button onClick={() => setValue(value + ".")}>.</button>
      </div>
      <div>
        <button onClick={() => setValue(value + "1")}>1</button>
        <button onClick={() => setValue(value + "2")}>2</button>
        <button onClick={() => setValue(value + "3")}>3</button>
        <button onClick={() => setValue(value + "3")}>0</button>
      </div>
      <div className="endbutton">
        <button onClick={() => setValue("")}>AC</button>
        <button onClick={() => calculate()}>=</button>
      </div>
      {/* <div>
        {value}
      </div> */}
    </div>
  );
}

export default Calculator;