import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  step: 0,
};

const counterSlice = createSlice({
  name: "counter",
  initialState,
  reducers: {
    increament: (state) => {
      state.step += 1;
    },
    decreament: (state) => {
      state.step -= 1;
    },
  },
});

export const { increament, decreament } = counterSlice.actions;

export default counterSlice.reducer