import React from "react";
import "../css/navbar.css";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <nav>
      <ul>
        <li>
          <NavLink to="">
            <i className="fa-solid fa-house-user"></i> Home
          </NavLink>
        </li>
        <li>
          <NavLink to="/login">
            <i className="fa-solid fa-plane-circle-check"></i> Login
          </NavLink>
        </li>
        <li>
          <NavLink to="/signup">
            <i className="fa-solid fa-right-to-bracket"></i> Signup
          </NavLink>
        </li>
        <li>
          <NavLink to="/calculator">
            <i className="fa-solid fa-calculator"></i> Calculator
          </NavLink>
        </li>
        <li>
          <NavLink to="/pagination">Pagination</NavLink>
        </li>
        <li>
          <NavLink to="/employeeList">employeeList</NavLink>
        </li>
        {/* <li>
          <NavLink to="/employeeDetails">employeeDetails</NavLink>
        </li> */}
        <li>
          <NavLink to="table">table</NavLink>
        </li>
        <li>
          <NavLink to="/dummy">Dummy</NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;