import React from "react";
import { useForm } from "react-hook-form";
import "../css/signup.css";

function Signup() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm(); // object destructuring
  const submitData = (data) => {
    console.log(data);
    fetch("https://jsonplaceholder.typicode.com/comments/1", {
      method: "PUT",
      body: JSON.strgify(data),
      headers: {
        "content-type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((data) => {
        // setTableData(data);
        console.log(data);
      });
  };
  return (
    <form className="signup" onSubmit={handleSubmit(submitData)}>
      <div className="label_1">
        <label htmlFor=""><i className="fa-solid fa-circle-user"></i> Username</label>
        <input
          type="text"
          {...register("username", {
            required: {
              value: true,
              message: "Username is required",
            },
            minLength: {
              value: 3,
              message: "Username should have at least three characters",
            },
            maxLength: {
              value: 8,
              message: "Username should have at most eight characters",
            },
          })}
        />
        {errors.username && <small>{errors.username.message}</small>}
      </div>
      <div className="label_2">
        <label htmlFor=""><i className="fa-solid fa-key"></i> Password</label>
        <input
          type="text"
          {...register("passWord", {
            required: {
              value: true,
              message: "Password is required",
            },
            pattern: {
              value: /^(?=.[A-Za-z])(?=.\d)(?=.[@$!%#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
              message:
                "Minimum eight characters, at least one letter, one number and one special character",
            },
            // minLength: {
            //   value: 8,
            //   message: "Password should have at least eight characters",
            // },
            // maxLength: {
            //   value: 16,
            //   message: "Password should have at most sixteen characters",
            // },
          })}
        />
        {errors.passWord && <small>{errors.passWord.message}</small>}
      </div>
      <div className="label_3">
        <label htmlFor=""><i className="fa-solid fa-unlock"></i> Re-password</label>
        <input type="text" {...register("repassWord", {
            required: {
              value: true,
              message: "Repassword is required",
            },
            minLength: {
              value: 8,
              message: "Repassword should have at least eight characters",
            },
            maxLength: {
              value: 16,
              message: "Repassword should have at most sixteen characters",
            },
          })} />
        {errors.repassWord && <small>{errors.repassWord.message}</small>}
      </div>
      <div className="submit">
        <button>Sign Up</button>
      </div>
    </form>
  );
}

export default Signup;