import React from "react";
import profile from "../asserts/emp.jpg";

function Card(props) {
  console.log(props);
  return (
    <div className="card">
      <div>
        <img src={profile} alt="img" />
      </div>
      <div className="sec">
        <p>{props.emp.Name}</p>
      </div>
      <div className="tr">
        <p>{props.emp.EmployeeID}</p>
        <button className="btn" onClick={() => props.btnFn()}>
          {props.btnName}
        </button>
      </div>
    </div>
  );
}

export default Card;