import React from "react";
import Card from "../components/card";
// import "../css/employeeDetails.css";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux"; 

function EmployeeDetails() {
  const navigate = useNavigate();
  // const employee = {
  //   Name: "Shea",
  //   Age: 56,
  //   Email: "at.velit.Pellentesque@felisadipiscingfringilla.org",
  //   "Date of joining": "17/06/2018",
  //   "Phone number": "1629100357399",
  //   Street: "6545 Sed Street",
  //   City: "Gap",
  //   Zip: "2152",
  //   Region: "PR",
  //   Country: "Tunisia",
  //   Info: "id sapien. Cras dolor dolor, tempus non, lacinia at, iaculis",
  //   EmployeeID: 108,
  // };

  const employee = useSelector(state => state.employee.employee)
  return (
    <div>
      <h1>EmployeeDetails</h1>
      <div className="employeeDetail">
        <Card
          emp={employee}
          btnFn={() => navigate("/employeeList")}
          className="card"
          btnName="Back"
        ></Card>
        <table className="tableDetail">
          <tbody>
            {Object.keys(employee).map((e, i) => (
              <tr key={i}>
                <th>{e}</th>
                <td>{employee[e]}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default EmployeeDetails;