import React from "react";
import profile from "../asserts/emp.jpg";

class Cardclass extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="card">
        <div className="row_one">
          <img src={profile} alt="employee" />
        </div>
        <div className="row_two">
          <p>{this.props.emp.Name}</p>
        </div>
        <div className="row_three">
          <p>{this.props.emp.EmployeeID}</p>
          <button className="btn" onClick={() => this.props.btnFn()}>
            {this.props.btnName}
          </button>
        </div>
      </div>
    );
  }
}

export default Cardclass;