import React from "react";
import { useForm } from "react-hook-form";
import Swal from "sweetalert2";
import "../css/login.css";

function Login() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const submitData = (data1) => {
    console.log(data1);
    fetch("https://jsonplaceholder.typicode.com/comments",{
      method:'POST',
      body:JSON.stringify(data1),
      headers: {
        'Content-Type':'application/json',
        'guna':123
      }
    })
      .then((res) => res.json())
      .then((data2) => {
        console.log(data2);
      });
      
    // Swal.fire({
    //   position: "top-right",
    //   icon: "success",
    //   title: "Logined Suscess Fully",
    //   // showConfirmButton: false,
    //   // timer: 1700,
    //   background:"linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1)",
    //   color: "black",
    //   iconColor: "pink"
    // });
    
  };
  return (
    <form className="login" onSubmit={handleSubmit(submitData)}>
      <div className="label_1">
        <label htmlFor="">
          <i className="fa-regular fa-circle-user"></i> Username
        </label>
        <input
          type="text"
          {...register("userName", {
            required: {
              value: true,
              message: "username is required",
            },
            minLength: {
              value: 3,
              message: "username should have atleast three characters",
            },
            maxLength: {
              value: 8,
              message: "username only have eight characters",
            },
          })}
        />
        {errors.userName && <small>{errors.userName.message}</small>}
      </div>
      <div className="label_2">
        <label htmlFor="">
          <i className="fa-solid fa-lock"></i> Password
        </label>
        <input
          type="text"
          {...register("passWord", {
            required: {
              value: true,
              message: "password must required",
            },
            pattern: {
              value:
                /^(?=.[A-Za-z])(?=.\d)(?=.[@$!%#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
              message:
                "Minimum eight characters, at least one letter, one number and one special character:",
            },
            // minLength: {
            //   value: 8,
            //   message: 'atleast 8 characters is required'
            // },
            // maxLength: {
            //   value: 16,
            //   message: 'maximum 16 characters only'
            // }
          })}
        />
        {errors.passWord && <small>{errors.passWord.message}</small>}
      </div>
      <div className="submit">
        <button>Login</button>
      </div>
    </form>
  );
}

export default Login;