import React from "react";
import "../../css/grid.css";
import Thead from "./thead";
function Grid(props) {
  const { data } = props;
  const keys = 
  Object.keys(data[0]);
  const [pageLimit, setPageLimit] = React.useState(10);
  const [page, setPage] = React.useState(1);
  const [lastPage, setLastPage] = React.useState(0);

  React.useEffect(() => {
    setLastPage(Math.ceil(data.length / pageLimit));
    setPage(1)
  }, [pageLimit]);

  // console.log(keys);

  const generateRow = (eachData) => {
    return keys.map((e) => <td key={e}>{eachData[e]}</td>);
  };

  const generateBody = (data) => {
    return data.map((eachData,i) => <tr key={i}>{generateRow(eachData)}</tr>);
  };

  const displayPage = () => {
    const start = (page - 1) * pageLimit;
    const end = start + pageLimit;
    const filterData = data.slice(start, end);
    return <tbody>{generateBody(filterData)}</tbody>;
  };

  const generateButtons = () => {
    const buttons = [];
    const start = page - 2 < 1 ? 1 : page - 2;
    const end = page + 2 < lastPage ? page + 2 : lastPage;
    for (let i = start; i <= end; i++) {
      buttons.push(i);
    }
    return buttons.map((e,i) => (
      <button
        className={e === page ? "activePage" : ""}
        onClick={() => setPage(e)}
        key={i}
      >
        {e}
      </button>
    ));
    // return (
    //   <>
    //     <button onClick={() => setPage(1)}>1</button>
    //     <button onClick={() => setPage(2)}>2</button>
    //     <button onClick={() => setPage(3)}>3</button>
    //     <button onClick={() => setPage(4)}>4</button>
    //     <button onClick={() => setPage(5)}>5</button>
    //   </>
    // );
  };
  return (
    <div>
      <div className="button">
        <button onClick={() => setPage(1)}>first</button>
        <button onClick={() => setPage(page - 1)} disabled={page === 1}>
          previous
        </button>
        {generateButtons()}
        <button onClick={() => setPage(page + 1)} disabled={lastPage === page}>
          next
        </button>
        <button onClick={() => setPage(lastPage)}>last</button>
        <select
          onChange={(e) => {
          //   console.log(e);
          //   console.log(e.target);
          //   console.log(e.target.value);
            setPageLimit(parseInt(e.target.value))
          }}
          value={pageLimit}
        >
          <option value="5">5</option>
          <option value="10">10</option>
          <option value="15">15</option>
          <option value="20">20</option>
        </select>
      </div>
      <table className="table">
        <Thead data={keys}></Thead>
        {displayPage()}
      </table>
    </div>
  );
}

export default Grid;